$primaryColor: #4527a4;

// Common CSS
html {
	scroll-behavior: smooth;
}

// Dashicon
.dashicons {
	font-family: dashicons !important;
}

.wp-block-mcb-mailchimp {
	width: 100%;
	display: flex;

	* {
		box-sizing: border-box;
	}

	.mcbMailChimp {

		.form {
			width: 100%;
			overflow: hidden;
			margin: 0;
			border: none;


			h2 {
				padding: 0px;
				text-align: center;
				width: auto;
			}

			p {
				margin: 0px;
				padding: 0px;
				margin-bottom: 15px;
				text-align: center;
			}

			input:focus {
				outline: none;
				box-shadow: none;
			}

			input[type="text"],
			input[type="email"] {
				min-height: 40px;
				box-sizing: border-box;
				margin: 0;
				border: unset;
			}

			input[type="email"] {}

			.submitBtn {
				position: relative;
				height: 40px;

				button {
					border: none;
					cursor: pointer;
					height: 100%;
				}

				.loader {
					position: absolute;
					top: 0;
					width: 100%;
					height: 100%;
					background: #5d5c5cbf;
					text-align: center;
				}
			}

			.noticeElement {
				>p {
					margin: 0 auto;
					padding: 5px 10px;
					color: #fff;
					font-weight: 500;
				}
			}
		}

		&.default {
			.form {
				margin: 0 auto;
				border-radius: 5px;

				h2 {}

				p {}

				input:focus {}

				input[type="text"],
				input[type="email"] {
					width: 100%;
					min-height: 40px;
				}

				input[type="email"] {}

				.emailBtnEle {
					display: flex;
					column-gap: 5px;
				}

				.submitBtn {

					button {
						width: 100%;

					}

					.loader {}
				}

				.noticeElement {

					>p {}

					.warning {}

					.success {}
				}
			}
		}

		&.form1 {

			position: relative;
			padding: 0px;
			background-color: transparent;

			.iconElement {
				position: absolute;
				width: 100%;
				top: -35px;
				left: 0;
				display: flex;
				align-items: center;
				justify-content: center;

				.icon {
					position: relative;
					width: 70px;
					height: 70px;
					background: #fff;
					border-radius: 50%;
					display: flex;
					align-items: center;
					justify-content: center;

					&::after {
						content: '';
						position: absolute;
						top: 50%;
						left: 50%;
						width: 50px;
						height: 50px;
						transform: translate(-50%, -50%);
						border: 2px solid #000;
						border-radius: 50%;
					}
				}
			}

			.form {
				margin: 0 auto;
				border-radius: 5px;

				h2 {}

				p {}

				input:focus {}

				.emailBtnElement {
					display: flex;
					align-items: center;
					justify-content: center;
					column-gap: 5px;
					margin-top: 30px;

					@media all and (max-width: 575px) {
						flex-direction: column;
					}

					input[type="email"] {
						margin-bottom: 0px;
						width: 100%;
						flex: 1;
					}

					.submitBtn {
						@media all and (max-width: 575px) {
							width: 100%;
						}

						button {
							@media all and (max-width: 575px) {
								width: 100%;
								margin-top: 15px;
							}
						}

						.loader {}
					}
				}

				.noticeElement {

					>p {}

					.warning {}

					.success {}
				}
			}
		}

		&.form2 {
			.iconElement {

				.icon {
					margin-bottom: 20px;
					text-align: center;
				}
			}

			.form {
				margin: 0 auto;
				border-radius: 5px;

				h2 {}

				p {}

				input:focus {}

				.emailBtnElement {
					position: relative;
					margin-top: 30px;

					input[type="email"] {
						margin-bottom: 0px;
						width: 100%;
					}

					.submitBtn {
						position: absolute;
						top: 0;
						right: 0;
						height: 100%;

						@media all and (max-width: 575px) {
							position: unset;
							width: 100%;
						}

						button {
							height: 100%;

							@media all and (max-width: 575px) {
								width: 100%;
							}
						}

						.loader {}
					}
				}

				.noticeElement {

					>p {}

					.warning {}

					.success {}
				}
			}
		}

		&.form3 {
			.form {
				margin: 0 auto;
				border-radius: 5px;

				h2 {}

				p {}

				input:focus {}

				.nameElement {
					display: flex;
					align-items: center;
					justify-content: center;
					column-gap: 20px;
					margin-top: 25px;

					@media all and (max-width: 575px) {
						flex-direction: column;
					}

					input[type="text"] {
						width: 100%;
						min-height: 40px;
					}
				}

				input[type="email"] {
					width: 100%;
					margin: 0px;
				}

				.submitBtn {

					button {
						width: 100%;
					}

					.loader {}
				}

				.noticeElement {

					>p {}

					.warning {}

					.success {}
				}
			}
		}

		&.form4 {
			.form {
				display: grid;
				grid-template-columns: 40% 60%;
				padding: 0;

				.left {
					display: flex;
					align-items: center;
					justify-content: center;
					flex-direction: column;
					padding: 0px 5px;
				}

				.right {
					input {
						width: 100%;
					}

					.submitBtn {
						button {
							width: 100%;
						}
					}
				}
			}
		}

		&.form5 {
			.form {
				.top {
					height: 200px;
				}

				.bottom {
					input {
						width: 100%;
					}

					.submitBtn {
						button {
							width: 100%;
						}
					}
				}
			}
		}

		&.form6 {
			.form {
				display: grid;
				grid-template-columns: 60% 40%;
				padding: 0;

				.right {
					display: flex;
					align-items: center;
					justify-content: center;
					flex-direction: column;
					padding: 0px 5px;
				}

				.left {
					input {
						width: 100%;
					}

					.submitBtn {
						button {
							width: 100%;
						}
					}
				}
			}
		}

		&.form7 {
			.form {
				// display: grid;
				// grid-template-columns: calc(30% - 3.33px) calc(50% - 3.33px) calc(20% - 3.34px);
				// gap: 5px;
				// justify-content: center;
				display: flex;
				justify-content: center;
				align-items: center;
				flex-direction: row;
				gap: 10px;


				h2 {
					margin-bottom: 0px !important;
					text-align: left !important;
					// flex-basis: 30%;
				}

				input {
					margin-bottom: 0px !important;
					flex-basis: 50%;
				}

				.submitBtn {
					height: 40px;

					button {
						height: 100%;
					}
				}
			}
		}

		&.form8 {
			.form {

				.top {
					padding: 15px 20px;
					display: flex;
					align-items: center;
					justify-content: center;
					gap: 20px;

					.left {

						h2 {
							text-align: left;
						}

						p {
							text-align: left;
						}
					}

					.right {
						.logo {
							// width: 120px;
							// height: 120px;

							img {
								width: 100%;
								height: 100%;
								object-fit: cover;
								border-radius: 50%;
							}
						}
					}
				}

				.bottom {

					.content {
						display: flex;
						align-items: center;
						gap: 10px;
						margin-bottom: 10px;

						.label {
							font-size: 16px;
							font-weight: 400;

						}

						input {
							margin-bottom: 0px !important;
							width: 100%;
						}
					}

					.submitBtn {
						button {
							width: 100%;
						}
					}
				}
			}
		}

		.popupBtnArea {
			display: flex;
			align-items: center;

			.popupBtn {
				font-size: 18px;
				font-weight: 400;
				padding: 4px 5px;
				display: block;
				font-family: 'Source Serif Pro';
				background: #fff;
				border: 1px solid #000;
				border-radius: 3px;
				cursor: pointer;
			}
		}


		.modalArea {

			.allForms {
				.closeIcon {
					position: absolute;
					top: -43px;
					right: 0px;
					background: #000;
					width: 40px;
					height: 40px;
					display: flex;
					align-items: center;
					justify-content: center;
					border-radius: 50%;
					cursor: pointer;
					z-index: 99999;


				}
			}

			&.modalAreaFBottom {
				position: fixed;
				bottom: 0;
				right: 0;
				z-index: 99999;

				.allForms {
					position: fixed;
					bottom: 0;
					right: 5px;
					margin: 0 auto;
					width: 21%;
					z-index: 99999;
				}
			}

			&.modalAreaNatural {
				position: fixed;
				top: 0;
				left: 0;
				background: #000000cf;
				width: 100%;
				height: 100%;
				z-index: 99999;

				.allForms {
					position: fixed;
					right: 0;
					left: 0;
					top: 45%;
					margin: 0 auto;
					transform: translateY(-50%);
					transition: all .3s ease;
					box-shadow: 5px 5px 12px 0px rgba(0, 0, 0, 0.1);
					border-radius: 7px;
					z-index: 99999;

					.closeIcon {
						top: -38px;
						right: -37px;
					}
				}
			}

			&.modalAreaFTop {
				position: fixed;
				top: 0;
				left: 0;
				z-index: 99999;

				.allForms {
					position: fixed;
					top: 0;
					left: 0;
					margin: 0 auto;
					background: #fff;
					transition: all .3s ease;
					box-shadow: 5px 5px 12px 0px rgba(0, 0, 0, 0.1);
					width: 100%;
					border-radius: 7px;
					z-index: 99999;

					.closeIcon {
						top: 5px;
						right: 10px;
					}
				}
			}
		}
	}

}